import { mapState } from 'vuex'
import { imgBaseUrl } from '@/common/helper.js'

export default {
	data() {
		return {
			imgUrl: ''
		}
	},
	methods: {
	},
	computed: {
		...mapState(['userInfo'])
	},
	created() {
		this.imgUrl = imgBaseUrl
	},
	onNavigationBarButtonTap(option) {
		if(option.index === 0) {
			uni.showActionSheet({
				itemList: ['保存到手机', '扫描二维码'],
				success(e) {
					console.log(e)
					switch(e.tapIndex) {
						case 0:
							console.log('保存到手机');
							break;
						case 1:
							console.log('扫描二维码');
							break;
						default:
							break;
					}
				},
				fail(err) {
					console.log(err)
				}
			})
		}
	}
}
